import * as React from 'react'
import styles from './PartnerInformation.less'

export const PartnerInformation = () => (
  <section className={styles.root}>
    <h1 className={styles.header}>Companies We've Helped</h1>
    <article>Some of the companies we've helped recruit excellent applicants over the years.</article>
    <div>
      <img src="https://s17314.pcdn.co/classic/wp-content/uploads/sites/2/2013/07/optimizely-logo-final.svg"/>
      <img src="https://s17314.pcdn.co/classic/wp-content/uploads/sites/2/2013/07/desk-logo-final.svg"/>
      <img src="https://s17314.pcdn.co/classic/wp-content/uploads/sites/2/2013/07/dribbble-logo-final.svg"/>
      <img src="https://s17314.pcdn.co/classic/wp-content/uploads/sites/2/2013/07/disqus-logo-final.svg"/>
      <img src="https://s17314.pcdn.co/classic/wp-content/uploads/sites/2/2013/07/dolby-logo-final.svg"/>
    </div>
  </section>
)

export default React.memo(PartnerInformation)



