import * as React from 'react'
import { List, Tag, Avatar, Icon } from 'antd'
import { IJob } from '@types'

export interface IJobItemProps {
  job: IJob
}

export const JobItem: React.SFC<IJobItemProps> = ({ job }) => (
  <List.Item>
    <List.Item.Meta
      avatar={<Avatar src = {job.avatar} /> }
      title={job.title}
      description={job.description}
    />
    <Tag><Icon type="environment" />{job.location}</Tag>
    <Tag color="#f50">{job.jobType}</Tag>
  </List.Item>
)

export default React.memo(JobItem)



