import * as React from 'react'
import { Row, Col, Icon } from 'antd'
import styles from './Footer.less'

export const Footer = () => (
  <React.Fragment>
    <Row gutter={16} className={styles.root} type="flex" justify="space-between">
      <Col xs={24} md={8}>
        <h3 className={styles.heading}>Nhan Viec Lam</h3>
        <div>Job Searching Just Got Easy.</div></Col>
      <Col xs={24} md={8}>
        <h3 className={styles.heading}>Office</h3>
        <div>350/34 Le Van Quoi Street, Binh Hung Hoa A Ward, Binh Tan District, Ho Chi Minh City, Viet Nam</div>
      </Col>
      <Col xs={24} md={8}>
        <h3 className={styles.heading}>Follow Us On</h3>
        <Icon type="facebook" />
      </Col>
    </Row>
    <div className={styles.copyright}>
      © 2018 Classic — All Rights Reserved
    </div>
  </React.Fragment>
)

export default React.memo(Footer)



