import * as React from 'react'
import { Layout } from 'antd'
import styles from './Home.less'
import {
  NavBar,
  Banner,
  PartnerInformation,
  CompanyInformation,
  ListJob,
  Footer,
  FooterContact,
} from '@components'

const Home = () => {
  return(
    <Layout className={styles.container}>
      <Layout.Header className={styles.header}>
        <NavBar/>
      </Layout.Header>
      <Layout.Content className={styles.content}>
        <Banner/>
        <PartnerInformation/>
        <CompanyInformation/>
        <ListJob/>
      </Layout.Content>
      <FooterContact/>
      <Layout.Footer className={styles.footer}>
        <Footer/>
      </Layout.Footer>
    </Layout >
  )
}

export default Home



