import * as React from 'react'
import { Row, List, Button } from 'antd'
import { JobItem } from '@components'
import { IJob } from '@types'
import styles from './ListJob.less'

export const renderItem = (job: IJob) => (
  <JobItem key={job.id} job={job}/>
)

export interface IListJobProps {
  jobs: IJob[]
}

export const ListJob: React.SFC<IListJobProps> = ({ jobs }) => (
  <Row className={styles.root}>
    <h1>Hundreds of Jobs From All Over the Globe</h1>
    <List
      className={styles.listJob}
      loadMore={<div className={styles.loadMoreButton}><Button type="primary">Load more</Button></div>}
      itemLayout="horizontal"
      dataSource={jobs}
      renderItem={renderItem}
    />
  </Row>
)

export default React.memo(ListJob)



