import * as React from 'react'
import styles from './FooterContact.less'

export const FooterContact = () => (
  <div className={styles.root}>
    <h1 className={styles.heading}>Got a question?</h1>
    <p className={styles.content}>
      We're here to help. Send us an email&nbsp;
      <a className={styles.email} href="mailto:tanphamhaiduong@gmail.com">
        tanphamhaiduong@gmail.com
      </a>
    </p>
  </div>
)

export default React.memo(FooterContact)



