export interface IWithListJobInner {}

export interface IWithListJobOutter {
  jobs: IJob[]
}
export const withListJob = mapProps<IWithListJobInner, IWithListJobOutter>(({ jobs }) => ({ jobs: mockJobs }))

export interface IEnhanceListJobInner{}

export interface IEnhanceListJobOutter{}

