import * as React from 'react'
import { Row, Col } from 'antd'
import styles from './CompanyInformation.less'

export const CompanyInformation = () => (
  <Row type="flex" justify="end" className={styles.root}>
    <Col offset = {12} className={styles.content}>
      <div className={styles.contentHeader}>Make Recruit Your Competitive</div>
      <div className={styles.contentDescription}>
        Talent is a top priority for all startup founders and executives.
        Jobify offers a way to completely optimize your entire recruiting process.
        Find better candidates, conduct more focused interviews, and make data-driven hiring decisions.
      </div>
    </Col>
  </Row>
)

export default React.memo(CompanyInformation)



